import breakfastData from '../data/breakfast.json'
import lunchData from '../data/lunch.json'
import dinnerData from '../data/dinner.json'

const siteContent = document.querySelector(".site-content")

function _clearSiteContent() {
    while (siteContent.firstChild) {
        siteContent.firstChild.remove();
    }
}

function _toggleSiteContentGrid(isGrid,) {
    if (!siteContent.classList.contains("site-content-grid")) {
        siteContent.classList.add("site-content-grid");
    } else {
        siteContent.classList.remove("site-content-grid");
    }
}

function _createReservationContent() {

}

function _createAboutContent(){
    const aboutSection = document.createElement("div")
    aboutSection.textContent = "This is where we would keep the about section... IF WE HAD ONE"
    siteContent.appendChild(aboutSection)
}

function _createMenuContent(menuData) {
    menuData.forEach(function(item){
        const menuItem = document.createElement("div");
        menuItem.classList.add("menu-item")

        const itemName = document.createElement("p");
        itemName.classList.add("menu-item-name");
        itemName.textContent = item.name;

        const itemDescription = document.createElement("p");
        itemDescription.classList.add("menu-item-description");
        itemDescription.textContent = item.description;

        const itemPrice = document.createElement("p");
        itemPrice.classList.add("menu-item-price");
        itemPrice.textContent = item.price;

        menuItem.appendChild(itemName);
        menuItem.appendChild(itemDescription);
        menuItem.appendChild(itemPrice);

        siteContent.appendChild(menuItem)
    })
}

function generateSiteContent(tabChoice) {
    _clearSiteContent();

    switch (tabChoice.toLowerCase()) {
        case "breakfast":
            _createMenuContent(breakfastData.menuItems)
            break;
        case "lunch":
            _createMenuContent(lunchData.menuItems)
            break;
        case "dinner":
            _createMenuContent(dinnerData.menuItems)
            break;
        case "reservations":
            _createReservationContent();
            break;
        case "about":
            _createAboutContent();
            break;
        default:
            _createMenuContent(breakfastData.menuItems);
    }
}

export { generateSiteContent }